//
// Custom mixins.
//

//
// Media Query mixin
//
// @param {String | Boolean} $from (false) - One of $mq-breakpoints
// @param {String | Boolean} $until (false) - One of $mq-breakpoints
// @param {String | Boolean} $and (false) - Additional media query parameters
// @param {String} $media-type ($mq-media-type) - Media type: screen, printâ€¦
// @param {String} $breakpoints ($mq-breakpoints) - Breakpoints map
//
@mixin mq(
	$from: false,
	$until: false,
	$and: false,
	$media-type: $mq-media-type,
	$breakpoints: $mq-breakpoints
) {
	$min-width: 0;
	$max-width: 0;
	$media-query: "";

	// From: this breakpoint (inclusive)
	@if $from {

		@if type-of($from) == number {
			$min-width: $from;
		} @else {
			$min-width: mq-get-breakpoint-width($from, $breakpoints);
		}
	}

	// Until: that breakpoint (exclusive)
	@if $until {

		@if type-of($until) == number {
			$max-width: $until;
		} @else {
			$max-width: mq-get-breakpoint-width($until, $breakpoints);
		}
	}

	@if $min-width != 0 {
		$media-query: "#{$media-query} and (min-width: #{$min-width})";
	}

	@if $max-width != 0 {
		$media-query: "#{$media-query} and (max-width: #{$max-width})";
	}

	@if $and {
		$media-query: "#{$media-query} and #{$and}";
	}

	// Remove unnecessary media query prefix 'all and '
	@if ($media-type == "all" and $media-query != "") {
		$media-type: "";
		$media-query: str-slice(unquote($media-query), 6);
	}

	@media #{$media-type + $media-query} {

		@content;
	}
}

//
// Hover focus mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin hover-focus {

	&:hover,
	&:focus,
	&:active {

		@content;
	}
}

//
// Border mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin border( $values ) {

	@include _directional-property(border, null, $values);
}

//
// Overlay mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin overlay {
	overflow: hidden;
	position: relative;

	&:before {
		display: block;
		background-color: $color--overlay;
		content: "";

		// @include position(absolute, 0 0 0 0);
	}
}

//
// Transition mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin transition {
	transition: all $base--transition-duration $base--transition-timing-function;
}

//
// Heading mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin heading {
	margin: 0 0 0.5em;
	font-family: $font-family--heading;
	font-weight: $font-weight--heading;
	line-height: $line-height--heading;
}

//
// Well mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin box {
	padding: $gap;
	border: $base--border;
	border-radius: $base--border--radius;
	background-color: $color--background;

	p {

		&:last-of-type {
			margin-bottom: 0;
		}
	}
}

//
// Notice mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin notice($color: "medium") {
	margin-bottom: $spacing--m;

	@include box;

	@if $color != "medium" {
		border-color: map_get($colors, $color);
		color: map_get($colors, $color);
		background-color: rgba(map_get($colors, $color), 0.1);
	}
}

//
// Icon mixin
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin icon($glyph: "\f005", $font_weight: 400) {
	font-family: "Font Awesome 5 Free", sans-serif;
	font-weight: $font_weight;
	content: $glyph;
}

//
// Button mixin
//
// Used for both front end and block editor styling.
//
// @group  Tools

// @link   https://github.com/seothemes/genesis-starter-theme
//

@mixin button {
	display: inline-block;
	width: auto;
	padding: $spacing--m $gutter;
	border: 0;
	border-radius: $base--border--radius;
	color: $color--white;
	background-color: $color--primary;
	font-size: $base--font-size / 1rem;
	font-weight: $font-weight--heading;
	line-height: 1;
	text-align: center;
	white-space: normal;
	text-decoration: none;
	cursor: pointer;

	@include transition();

	@include hover-focus {
		outline: none;
		color: $color--white;
		background-color: lighten($color--primary, 10);
		text-decoration: none;
	}

	&:disabled {

		&,
		&:hover,
		&:focus {
			opacity: 0.5;
			background-color: $color--muted;
			cursor: not-allowed;
		}
	}

	&.small {
		padding: 1em 1.5em;
		font-size: small;
	}

	&.rounded {
		border-radius: 10rem;
	}

	&.outline {
		border: 1px solid $color--primary;
		color: $color--primary;
		background: transparent;

		@include hover-focus {
			color: $color--white;
			background: $color--primary;
		}
	}

	&.fa,
	&.fab,
	&.fas {
		display: inline-flex;
		font-family: $base--font-family;
		font-weight: $font-weight--heading;

		&:before {
			display: inline-block;
			margin-right: $spacing--s;
			font-family: "Font Awesome 5 Pro", sans-serif;
			font-style: normal;
			font-variant: normal;
			line-height: 1;
			text-rendering: auto;
		}
	}

	&.icon-right {

		&:before {
			order: 1;
			margin-right: 0;
			margin-left: $spacing--s;
		}
	}
}

@mixin _directional-property(
  $property,
  $suffix,
  $values
) {
  $top: $property + "-top" + if($suffix, "-#{$suffix}", "");
  $bottom: $property + "-bottom" + if($suffix, "-#{$suffix}", "");
  $left: $property + "-left" + if($suffix, "-#{$suffix}", "");
  $right: $property + "-right" + if($suffix, "-#{$suffix}", "");
  $all: $property + if($suffix, "-#{$suffix}", "");

  $values: _compact-shorthand($values);

  @if _contains-falsy($values) {
    // @if nth($values, 1) { #{$top}: nth($values, 1); }

    @if length($values) == 1 {
      @if nth($values, 1) { #{$right}: nth($values, 1); }
    } @else {
      @if nth($values, 2) { #{$right}: nth($values, 2); }
    }

    @if length($values) == 2 {
    //   @if nth($values, 1) { #{$bottom}: nth($values, 1); }
      @if nth($values, 2) { #{$left}: nth($values, 2); }
    } @else if length($values) == 3 {
      @if nth($values, 3) { #{$bottom}: nth($values, 3); }
      @if nth($values, 2) { #{$left}: nth($values, 2); }
    } @else if length($values) == 4 {
      @if nth($values, 3) { #{$bottom}: nth($values, 3); }
      @if nth($values, 4) { #{$left}: nth($values, 4); }
    }
  } @else {
    #{$all}: $values;
  }
}

@mixin margin($values) {
  @include _directional-property(margin, null, $values);
}

@mixin padding($values) {
  @include _directional-property(padding, null, $values);
}

@mixin hide-visually($toggle: "hide") {
  @if not index("hide" "unhide", $toggle) {
    @error "`#{$toggle}` is not a valid value for the `$toggle` argument in " +
      "the `hide-visually` mixin. Must be either `hide` or `unhide`.";
  } @else if $toggle == "hide" {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(100%);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  } @else if $toggle == "unhide" {
    clip: auto;
    clip-path: none;
    height: auto;
    overflow: visible;
    position: static;
    white-space: inherit;
    width: auto;
  }
}

@mixin size(
  $width,
  $height: $width
) {
  @if _is-size($height) {
    height: $height;
  } @else {
    @error "`#{$height}` is not a valid length for the `$height` argument " +
      "in the `size` mixin.";
  }

  @if _is-size($width) {
    width: $width;
  } @else {
    @error "`#{$width}` is not a valid length for the `$width` argument " +
      "in the `size` mixin.";
  }
}

@mixin position(
  $position,
  $box-edge-values
) {
  $box-edge-values: _unpack-shorthand($box-edge-values);
  $offsets: (
    "top": nth($box-edge-values, 1),
    "right": nth($box-edge-values, 2),
    "bottom": nth($box-edge-values, 3),
    "left": nth($box-edge-values, 4),
  );

  position: $position;

  @each $offset, $value in $offsets {
    @if _is-length($value) {
      #{$offset}: $value;
    }
  }
}

@mixin clearfix {
  &::after {
    clear: both;
    content: "";
    display: block;
  }
}